import { newSpecPage } from '@stencil/core/testing';
import { h } from '@stencil/core';
import { PrestoSearchBarUi } from '../../ui/presto-search-bar-ui';

describe('presto-search-bar-ui', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoSearchBarUi],
      template: () => <presto-search-bar-ui></presto-search-bar-ui>,
    });
    await page.waitForChanges();
    expect(page.root).toMatchSnapshot();
  });

  it('renders with placeholder', async () => {
    const page = await newSpecPage({
      components: [PrestoSearchBarUi],
      template: () => <presto-search-bar-ui placeholder="Search"></presto-search-bar-ui>,
    });
    await page.waitForChanges();
    expect(page.root).toMatchSnapshot();
  });

  it('renders with a value', async () => {
    const page = await newSpecPage({
      components: [PrestoSearchBarUi],
      template: () => <presto-search-bar-ui value="search term"></presto-search-bar-ui>,
    });
    await page.waitForChanges();
    expect(page.root).toMatchSnapshot();
  });

  it('renders with arrow controls', async () => {
    const page = await newSpecPage({
      components: [PrestoSearchBarUi],
      template: () => <presto-search-bar-ui hasNavigation={true}></presto-search-bar-ui>,
    });
    await page.waitForChanges();
    expect(page.root).toMatchSnapshot();
  });
});
